import { NgModule }     from '@angular/core'
import { CommonModule } from '@angular/common'
import { ReactiveFormsModule } from '@angular/forms'

import { PostsComponent }         from './posts.component'
import { PostEditComponent }      from './post-edit/post-edit.component'
import { PostStartComponent }     from './post-start/posts-start.component'
import { postsRouting }       from './posts.routing';
import { PostDetailsComponent } from './post-details/post-details.component'
import { SpinnerComponent }   from 'app/shared/spinner.component';
import { PostItemDetailsComponent } from './post-item-details/post-item-details.component'
import { UpperCaseDirective } from 'app/shared/upper-case.directive'

@NgModule({
  declarations: [
    PostsComponent,
    PostStartComponent,
    PostEditComponent,
    PostDetailsComponent,
    SpinnerComponent,
    PostItemDetailsComponent
  ],
  imports:[
      CommonModule,
      ReactiveFormsModule,
      postsRouting
  ]
})
export class PostsModule{}


