module.exports = "<div class=\"container\">\n  <div class=\"panel-group\" *ngIf=\"selectedPost\">\n    <div class=\"panel panel-primary\">\n      <div class=\"panel-heading\">\n        <h4 class=\"panel-title\">\n          <a class=\"accordion-toggle\" data-parent=\"#accordion\" data-toggle=\"collapse\" href=\"#collapse\"> {{selectedPost?.postDescription}} </a>\n        </h4>\n      </div>\n      <div id=\"collapse\" class=\"panel-collapse collapse in\">\n        <div class=\"panel-body\">\n          Address               : {{selectedPost.address}}, {{selectedPost.location}} , {{selectedPost.locationZipcode}}\n          <br>Publish From          : {{selectedPost.publishFrom}}\n          <br>Publish Till          : {{selectedPost.publishTill}}\n          <br>Contact No            : {{selectedPost.contactNo}}\n          <br>Additional Notes      : {{selectedPost.additionalNotes}}\n          <br>External images Link  : {{selectedPost.externalImagesLink}}\n          <a *ngIf=\"selectedPost.externalImagesLink.length>0\" \n            href=\"{{selectedPost.externalImagesLink}}\" \n            target=\"_blank\">Click</a>\n          \n          <br>\n            <i class=\"glyphicon \" \n            [ngClass]=\"selectedPost.petFree?'glyphicon-ok':'glyphicon-remove'\"> Pet Free |</i>   \n            <i class=\"glyphicon \" \n            [ngClass]=\"selectedPost.smokeFree?'glyphicon-ok':'glyphicon-remove'\"> Smoke Free |</i>   \n            <i class=\"glyphicon \" \n            [ngClass]=\"selectedPost.pickupFree?'glyphicon-ok':'glyphicon-remove'\"> Pickup Free</i>   \n        </div>\n        <div class=\"panel-footer\">Panel Footer</div>\n      </div>\n    </div>\n  </div>\n  <br>\n  <!--Items-->\n  <div class=\"panel-group\" id=\"accordion\">\n  <div class=\"panel panel-primary\">\n    <div class=\"panel-heading\">\n      <h4 class=\"panel-title\">\n        <a class=\"accordion-toggle\" data-toggle=\"collapse\" data-parent=\"#accordion\" href=\"#collapseItems\">\n          Items for sale\n        </a>\n      </h4>\n    </div>\n    <div id=\"collapseItems\" class=\"panel-collapse collapse\">\n      <div class=\"panel-body\">\n        <a *ngIf=\"!addItem\" class=\"glyphicon glyphicon-plus\" \n           style=\"font-family: Lato, Helvetica Neue, Helvetica, Arial, sans-serif;\"\n            (click)=\"onNewItem()\" > Add Item</a>    \n\n        <div *ngIf=\"addItem\" >\n          <form [formGroup]=\"itemDetailsForm\" class=\"form-horizontal\" (ngSubmit)=\"onAddItem()\">\n            <div [hidden]=\"itemDetailsForm.valid\" >\n              <p style=\"color: #a94442\">Please enter required fields</p>\n            </div>\n            <div class=\"form-group\">\n              <label for=\"description\" class=\"col-sm-2 control-label\">Item Description</label>\n              <div class=\"col-sm-10\">\n                <input type=\"text\" class=\"form-control\" id=\"description\" \n                formControlName=\"description\"\n                [(ngModel)]=\"tempPostItemDetail.description\"\n                placeholder=\"Item description\" required>\n              </div>\n            </div>\n            <div class=\"form-group\">\n              <label for=\"price\" class=\"col-sm-2 control-label\">Price</label>\n              <div class=\"col-sm-3\">\n                <input type=\"number\" class=\"form-control\" id=\"price\" \n                formControlName=\"price\"\n                [(ngModel)]=\"tempPostItemDetail.price\"\n                placeholder=\"0.00\" required>\n              </div>\n              <label for=\"year\" class=\"col-sm-2 control-label\">Manufactured Year</label>\n              <div class=\"col-sm-3\">\n                <input type=\"number\" class=\"form-control\" id=\"year\" \n                formControlName=\"year\"\n                [(ngModel)]=\"tempPostItemDetail.year\"\n                placeholder=\"\" required>\n              </div>\n            </div>\n            <div class=\"form-group\">\n              <label for=\"externalLink\"  class=\"col-sm-2 control-label\">Product reference link</label>\n              <div class=\"col-sm-10\">\n                <input type=\"text\" class=\"form-control\" id=\"externalLink\" \n                formControlName=\"externalLink\"\n                [(ngModel)]=\"tempPostItemDetail.externalLink\">\n              </div>\n            </div>\n            <div class=\"form-group\">\n              <label for=\"pictureURL\" class=\"col-sm-2 control-label\">Picture</label>\n              <div class=\"col-sm-10\">\n                <input type=\"file\" multiple (change)=\"displayImages(input)\" #input \n                accept=\"image/*\" #imagePath\n                formControlName=\"pictureURL\"  \n                class=\"form-control\" id=\"pictureURL\"\n                />\n              </div>\n            </div>\n            <div  class=\"row\">\n              <label class=\"col-sm-2 control-label\"></label>\n              <div class=\"col-lg-3 col-md-4 col-xs-6 thumb\" \n                  *ngFor=\"let file_src of file_srcs; let i = index\">\n                  <a class=\"thumbnail\" >\n                      <img class=\"img-responsive\" [attr.src]='file_src' alt=\"\"/>\n                  </a>\n              </div>\n            </div>\n\n            <button type=\"submit\" class=\"btn btn-primary\" >Add</button>\n            <button type=\"button\" class=\"btn btn-default\" (click)=\"onCancel()\">Cancel</button>\n            <button type=\"reset\" class=\"btn btn-default\" >Reset</button>\n          </form>\n        </div>\n\n        <spinner [visible] = \"onSubmit\" [comments]=\"onSubmitProgress\"></spinner>  \n\n        <div style=\"margin-top:10px \" *ngFor=\"let item of items\">\n          <div class = \"list-group-item\">\n            Description               : {{item.description}}\n            <br>price                 : {{item.price}}\n            <br>year                  : {{item.year}}\n            <br><a *ngIf=\"item.externalLink.length>0\" href=\"{{item.externalLink}}\" target=\"_blank\">Reference Link</a>\n            <br>\n\n            <div  class=\"row\">\n              <div class=\"col-lg-3 col-md-4 col-xs-6 thumb\" \n                  *ngFor=\"let file_src of item.pictureURL; let i = index\">\n                  <a class=\"thumbnail\" >\n                      <img class=\"img-responsive\" [attr.src]='file_src' alt=\"\"/>\n                  </a>\n              </div>\n            </div>\n        \n          </div>\n        </div>\n      </div>\n    </div>\n  </div>\n</div>"

