module.exports = "<br>\n<div class=\"container col-md-7\" >\n<div style=\"color: #a94442\" [hidden]=\"postForm.valid\">\n  <p>Please enter all required fields\n</div>\n<form [formGroup]=\"postForm\" (ngSubmit)=\"onSubmit()\" >\n<!--Post fields-->\n<fieldset class=\"name-border\">\n  <legend class=\"name-border\">Post Details</legend>\n  <div class=\"form-field\">\n    <label for=\"postDescription\">Description</label>\n    <input type=\"text\" class=\"form-control\" id=\"postDescription\" \n    formControlName=\"postDescription\" \n    placeholder=\"Please enter description of this post\" required>\n  </div>  \n  <div class=\"form-group\">\n    <label for=\"publishFrom\">Publish From</label>\n    <input type=\"date\" class=\"form-control \" style=\"width: 180px\" id=\"publishFrom\"\n    formControlName=\"publishFrom\" required   >\n  </div>\n  <div class=\"form-group\">\n    <label for=\"publishTill\">Publish Till</label>\n    <input type=\"date\" class=\"form-control\" style=\"width: 180px\" id=\"publishTill\"\n    formControlName=\"publishTill\" required   >\n  </div>\n</fieldset>\n<fieldset class=\"name-border\">\n  <legend class=\"name-border\">Address</legend>\n\n  <div class=\"form-group\">\n    <label for=\"locationZipCode\">Zip Code</label>\n    <input type=\"text\" class=\"form-control\" id=\"locationZipCode\" \n    formControlName=\"locationZipCode\" required #zip\n    minlength=\"5\" maxlength=\"8\"\n    (change)=\"loadCityState(zip.value)\">\n  </div>\n  <div class=\"form-group\">\n    <label for=\"locationCity\">City</label>\n    <input type=\"text\" class=\"form-control\" id=\"locationCity\"\n    disabled=\"true\"\n    formControlName=\"locationCity\" required>\n  </div>\n  <div class=\"form-group\">\n    <label for=\"locationState\">State</label>\n    <input type=\"text\" class=\"form-control\" id=\"locationState\" \n    disabled=\"true\"\n    formControlName=\"locationState\"  required>\n  </div>\n  <div class=\"form-group\">\n    <label for=\"locationAddress\">Address</label>\n    <input type=\"text\" class=\"form-control\" id=\"locationAddress\" \n    formControlName=\"locationAddress\" >\n  </div>\n  <div class=\"form-group\">\n    <label for=\"contactNumber\">Contact Number</label>\n    <input type=\"text\" class=\"form-control\" id=\"contactNumber\" \n    formControlName=\"contactNumber\">\n  </div>\n</fieldset>\n<fieldset class=\"name-border\">\n<legend class=\"name-border\">Additional details</legend>\n\n  <div class=\"form-group\">\n    <label for=\"additionalNotes\">Additional Notes</label>\n    <input type=\"text\" class=\"form-control\" id=\"additionalNotes\" \n    formControlName=\"additionalNotes\">\n  </div>\n  <div class=\"form-group\">\n    <label for=\"externalImagesLink\">External Images Link</label>\n    <input type=\"text\" class=\"form-control\" id=\"externalImagesLink\" \n    formControlName=\"externalImagesLink\">\n  </div>\n  <div class=\"checkbox\">\n    <label>\n      <input type=\"checkbox\" id = \"petFree\" \n      formControlName=\"petFree\"\n      > Pet Free\n    </label>\n  </div>\n  <div class=\"checkbox\">\n    <label>\n      <input type=\"checkbox\" id = \"smokeFree\" \n      formControlName=\"smokeFree\"> Smoke Free\n    </label>\n  </div>\n  <div class=\"checkbox\">\n    <label>\n      <input type=\"checkbox\" id = \"pickupFree\" \n      formControlName=\"pickupFree\"> Pickup Free\n    </label>\n  </div>\n</fieldset>\n<!--Post fields ends-->\n  <button type=\"submit\" class=\"btn btn-primary\" >Submit</button>\n  <button type=\"reset\" class=\"btn btn-default\" (click)=\"onCancel()\">Cancel</button>\n</form>\n</div>"

