import { Component, Input } from '@angular/core';

@Component({
    selector: 'spinner',
    template: `
        <div *ngIf="visible">
    	    <i  class="fa fa-spinner fa-spin fa-3x"></i>{{comments}}
        </div>
    `
})
export class SpinnerComponent { 
    @Input() visible = true; 
    @Input() comments?="";
}


